@import "../../scss/variables.scss";

.todo-header__title {
  position: absolute;
  top: -80px;
  width: 100%;
  font-size: 80px;
  text-align: center;
  color: $light-red;

  @media (max-width: 350px) {
    top: -60px;
    font-size: 70px;
  }
}

.todo-header__choice-all {
  position: absolute;
  width: 1px;
  height: 1px;
  opacity: 0;

  & + label {
    position: absolute;
    top: 18px;
    left: 7px;
    width: 25px;
    height: 25px;
    text-align: center;
    font-size: 40px;
    transform: rotate(-90deg);
  }
}

.todo-header__input {
  box-sizing: border-box;
  width: 100%;
  padding: 15px;
  padding-left: 60px;
  font-size: 1.6em;
  line-height: 1.4em;
  box-shadow: inset 0 -1px 1px $lighter-black;

  &::placeholder {
    font-style: italic;
    color: $light-black;
  }
}
